<template>
  <main class="content container" v-if="productLoading">Загрузка...</main>
  <main class="content container" v-else-if="!productData">
    Ошибка загрузки!
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item breadcrumbs__link">
          <!-- <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> -->
          {{ category.title }}
          <!-- </router-link> -->
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="product.image.file.url"
            :alt="product.title"
          />
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="doAddToCart"
          >
            <b class="item__price">Цена: {{ pricePretty }} ₽ </b>

            <div class="item__row">
              <div class="form__counter">
                <button
                  type="button"
                  aria-label="Убрать один товар"
                  @click.prevent="
                    {
                      if (productAmount > 1) productAmount--;
                    }
                  "
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount" />

                <button
                  type="button"
                  aria-label="Добавить один товар"
                  @click.prevent="productAmount++"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button
                class="button button--primery"
                type="submit"
                :disabled="productAddSending"
              >
                В корзину
              </button>

              <span v-if="productAmount > 1">
                Итого:
                {{ Intl.NumberFormat().format(product.price * productAmount) }}
                ₽</span
              >

              <BaseModal
                v-model:open="isShowAddedMessage"
                to="#teleport-target"
              >
                Товар добавлен в корзину
              </BaseModal>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"> Описание </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Характеристики </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Гарантия </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Оплата и доставка </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br />
            Синхронизация со смартфоном<br />
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br />
            Поддержка сторонних приложений<br />
          </p>

          <a href="#"> Все характеристики </a>

          <h3>Что это?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет
            оптимизировать свои велотренировки, сделав их максимально
            эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по
            ANT+, объединяя полученную с них информацию. Данные отображаются на
            дисплее, а также сохраняются на смартфоне. При этом на мобильное
            устройство можно установить как фирменное приложение, так и
            различные приложения сторонних разработчиков. Велокомпьютер точно
            отслеживает местоположение, принимая сигнал с целого комплекса
            спутников. Эта информация позволяет смотреть уже преодоленные
            маршруты и планировать новые велопрогулки.
          </p>

          <h3>Дизайн</h3>

          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства
            составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты
            смартфона. Корпус гаджета выполнен из черного пластика. На
            обращенной к пользователю стороне расположен дисплей диагональю 56
            мм. На дисплей выводятся координаты и скорость, а также полученная
            со смартфона и синхронизированных датчиков информация:
            интенсивность, скорость вращения педалей, пульс и т.д. (датчики не
            входят в комплект поставки). Корпус велокомпьютера имеет степень
            защиты от влаги IPX7. Это означает, что устройство не боится пыли, а
            также выдерживает кратковременное (до 30 минут) погружение в воду на
            глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import BaseModal from "@/components/BaseModal.vue";
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import { API_BASE_URL } from "@/config";
import { useStore } from "vuex";
import { computed, defineComponent, ref } from "vue";
import { useRoute } from "vue-router";

export default defineComponent({
  components: {
    BaseModal,
  },
  setup() {
    const $store = useStore();
    const $route = useRoute();

    const productAmount = ref(1);
    const productData = ref(null);
    const pricePretty = computed(() => {
      return numberFormat(product.value.price);
    });
    const product = computed(() => {
      return productData.value;
    });
    const category = computed(() => {
      return productData.value.category;
    });

    const productAdded = ref(false);
    const productAddSending = ref(false);
    const isShowAddedMessage = ref(false);
    const doAddToCart = () => {
      productAdded.value = false;
      productAddSending.value = true;

      $store
        .dispatch("addProductToCart", {
          productId: product.value.id,
          amount: productAmount.value,
        })
        .then(() => {
          isShowAddedMessage.value = true;
          productAdded.value = true;
          productAddSending.value = false;
        });
    };

    const productLoading = ref(false);
    const productLoadingFailed = ref(false);
    const doLoadProduct = () => {
      productLoading.value = true;
      productLoadingFailed.value = false;
      axios
        .get(API_BASE_URL + "/api/products/" + $route.params.id)
        .then((response) => (productData.value = response.data))
        .catch(() => (productLoadingFailed.value = true))
        .then(() => (productLoading.value = false));
    };

    doLoadProduct();

    return {
      productAmount,
      productData,
      productLoading,
      productLoadingFailed,
      productAdded,
      productAddSending,
      isShowAddedMessage,
      pricePretty,
      product,
      category,
      doAddToCart,
      doLoadProduct,
    };
  },
});
</script>
