<template>
     <Teleport v-if="open" to="#teleport-target">
            <div class="teleport-blackout"></div>
            <div class="teleport-modal" @click="onOutsideClick">
                <div ref="content" class="teleport-modal__content">
                    <!-- <button type="button" class="teleport-modal__close" @click="doClose">X Close</button> -->
                    <slot></slot>
                </div>       
            </div>
          </Teleport>
</template>

<script>
export default {
    props: {
        open: {type: Boolean},
    },
    methods: {
        onOutsideClick($event) {
            if($event.target !== this.$refs.content && $event.target.contains(this.$refs.content)) this.doClose();
        },
        doClose() {
            this.$emit('update:open', false);
        }
    }
}
</script>

<style scoped>
